gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);

ScrollTrigger.defaults({
  toggleActions: 'restart pause resume pause',
});

gsap.utils.toArray('.nav-link a').forEach(function(a) {
  a.addEventListener('click', function(e) {
    e.preventDefault();
    gsap.to(window, { duration: 1, scrollTo: e.target.getAttribute('href') });
  });
});

const targets = document.querySelectorAll('#container div');

targets.forEach(target => {
  gsap
    .timeline({
      defaults: { duration: 1 },
      scrollTrigger: {
        trigger: target,
        scrub: true,
        start: 'center 80%',
        end: 'center 20%',
      },
    })
    .fromTo(target, { y: 50 }, { y: -50 })
    .from(target, { opacity: 0, duration: 0.2 }, 0)
    .to(target, { opacity: 0, duration: 0.2 }, 0.8);
});

gsap.utils.toArray('.demo-text').forEach((section, index) => {
  const w = section.querySelector('.wrapper');

  const [x, xEnd] = index % 2 ? ['100%', (w.scrollWidth - section.offsetWidth) * -1] : [w.scrollWidth * -1, 0];
  gsap.fromTo(
    w,
    { x },
    {
      x: xEnd,
      scrollTrigger: {
        start: 'top 80%',
        trigger: section,
        scrub: 0.5,
        // markers: true,
      },
    }
  );
});

gsap.registerPlugin(TextPlugin);

function randomPick(arr) {
  const selected = arr[Math.floor(Math.random() * arr.length)];
  return selected;
}
const txt = ['RUF#MICH#AN'];

const selText = randomPick(txt);

const tl2 = gsap.timeline({
  scrollTrigger: {
    trigger: '.test6',
    // scrub: 5,
    // pin: true,
    start: '-=450',
    end: '-=500',
    toggleActions: 'restart none none reverse',
    // markers: true,
  },
});

tl2
  .to('.test6', {
    x: 20,
    color: 'white',
    text: 'YTKYTKYTKYTKYTKYTKYTKYTKYTK',
    scale: 1,
    duration: 5,
    delay: 5,
  })
  .to('.test6', {
    color: 'white',
    text: selText,
    scale: 1,
    duration: 3,
    delay: 3,
  });
